import React from 'react'
import Link from 'gatsby-link'

const IndexPage = ({data}) => {
  const { edges: posts } = data.allMarkdownRemark

  return (
    <div className='flex flex-column justify-center items-center'>
      <h1 className='f3 f2-ns fw7 lh-title mt0 mb3 avenir'>Hello.</h1>
      <h4 className='f4 f3-ns fw4 lh-title mt0 avenir'>I'm Taylor Bell.</h4>

      <div className='flex flex-column justify-center items-center'>
        {posts
          .map(({node: post}) => {
            return (
              <div className='blog-post-preview w-100' key={post.id}>
                <h1 className='avenir f3 f2-ns fw3 mb0'>
                  <Link to={post.frontmatter.path} className='dark-gray'>
                    {post.frontmatter.title}
                  </Link>
                </h1>
                <p className='avenir mt1 moon-gray'>{post.frontmatter.date}</p>
                <p className='avenir'>{post.frontmatter.excerpt}</p>
                <ul className='moon-gray'>
                  {post.frontmatter.tags.sort().map((tag, i) => {
                    return (
                      <li key={i}>
                        <Link to={`/tags/${tag}`} className='avenir dark-gray o-70'>
                          {tag}
                        </Link>
                      </li>
                    )
                  })}
                </ul>
              </div>
            )
          })
          }
      </div>
    </div>
  )
}

export const pageQuery = graphql`
  query IndexQuery {
    allMarkdownRemark(sort: { order: DESC, fields: [frontmatter___date] }) {
      edges {
        node {
          id
          frontmatter {
            title
            date(formatString: "MMMM DD, YYYY")
            path
            tags
            excerpt
          }
        }
      }
    }
  }
`

export default IndexPage



